export default "import { Cell, Form, Grid, HFlow, TextField } from '../../../../lib'\n\nfunction TextInput() {\n  const renderForm = () => {\n    return (\n      <Grid>\n        <Cell size={6}>\n          <TextField\n            name='empty'\n            label='Name'\n            placeholder='Ex: Daenerys Stormborn of the House Targaryen First of Her Name'\n            required\n          />\n        </Cell>\n        <Cell size={3}>\n          <TextField name='disabled' label='Email' disabled />\n        </Cell>\n        <Cell size={3}>\n          <TextField name='password' label='Password' type='password' required />\n        </Cell>\n      </Grid>\n    )\n  }\n\n  return <Form render={renderForm} />\n}\n\nexport default TextInput\n"